* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

input {
  border-radius: 10px;
  margin: 10px;
  padding: 10px;
  font-size: 25px;
}

label {
  color: #0c0c4d;
  font-size: 25px;
}

.submit {
  color: #fff;
  background: #156705;
  border: none;
  border-radius: 10px;
  padding: 10px;
  font-size: 25px;
}

#datetime-picker {
  text-align: center;
  color: #87ceeb;
  border: 1px solid #87cefa;
  border-radius: 15px;
  margin-bottom: 20px;
  padding: 10px 15px;
  font-size: 40px;
}

.timer {
  width: 600px;
  justify-content: space-around;
  display: flex;
}

.field {
  color: #5393ba;
  justify-items: center;
  margin-right: 30px;
  display: inline-grid;
}

.value {
  font-size: 40px;
}

.label {
  text-transform: uppercase;
  font-size: 20px;
}

.start-timer {
  color: #fff;
  background-color: #87cefa;
  border: none;
  border-radius: 15px;
  padding: 10px 15px;
  font-size: 40px;
}

.start-timer:hover {
  background-color: #51b3f0;
}

.start-timer:disabled {
  background: #a09e9e;
}

.switcher-start {
  color: #fff;
  background: #06a895;
  border: none;
  border-radius: 15px;
  padding: 15px;
  font-size: 25px;
}

.switcher-start:hover {
  background: #04693d;
}

.switcher-stop {
  color: #fff;
  background: #eea526;
  border: none;
  border-radius: 15px;
  padding: 15px;
  font-size: 25px;
}

.switcher-start:disabled, .switcher-stop:disabled {
  background: #8c8b8b;
}

